<template>
    <label
        class="title lock-page-swipe"
        v-touch:swipe.right.stop="scrollLeft"
        v-touch:swipe.left.stop="scrollRight"
        ref="containerRef"
    >
        <Img v-if="props.title?.img"
            v-bind="props.title.img"
            :style="{ width: props.title.img.width }"
        />
        <span class="title-text" v-if="props.title">{{ props.title?.text || t(props.title) }}</span>

        <template v-for="item in props.items" :key="item.code">
            <Button
                :name="t(item.name)"
                class="size-s menu"
                :class="{ active: props.value === item.code }"
                :ref="el => setButtonRef(item.code, el)"
                @click="setValue(item.code)"
            />
        </template>
    </label>
</template>

<script setup>
    import { ref, watch, nextTick, onMounted } from 'vue';
    import { useI18n } from 'vue-i18n';
    import Button from 'ðŸ”—/components/ui/Button.vue';
    import Img from 'ðŸ”—/components/ui/Img.vue';
    import stores from '@/stores';

    const locale = stores.locale();

    const { t } = useI18n();

    const props = defineProps({
        title: { type: [Object, String, null], default: null },
        value: { type: [String, null], default: null },
        items: { type: Array, default: () => [] }
    });

    const emit = defineEmits(['change']);

    const containerRef = ref(null);
    const buttonRefs = ref({});

    const setButtonRef = (code, el) => {
        if (el) {
            buttonRefs.value[code] = el;
        }
    };

    const scrollToCenter = (code) => {
        nextTick(() => {
            const container = containerRef.value;
            const componentInstance = buttonRefs.value[code];
            const button = componentInstance?.el;

            if (!container || !button) {
                return;
            }

            const containerRect = container.getBoundingClientRect();
            const buttonRect = button.getBoundingClientRect();

            const offset = (buttonRect.left + buttonRect.width / 2) - (containerRect.left + containerRect.width / 2);

            container.scrollTo({
                left: container.scrollLeft + offset,
                behavior: 'smooth',
            });
        });
    };

    const setValue = (value) => {
        if (props.value !== value) {
            emit('change', value);
            scrollToCenter(value);
        }
    };

    const scrollLeft = () => {
        const container = containerRef.value;
        if (!container) {
            return;
        }

        container.scrollTo({
            left: 0,
            // left: !locale.rtl ? container.scrollWidth : 0,
            behavior: 'smooth',
        });
    };


    const scrollRight = () => {
        const container = containerRef.value;
        if (!container) {
            return;
        }

        container.scrollTo({
            left: !locale.rtl ? container.scrollWidth : -container.scrollWidth,
            behavior: 'smooth',
        });
    };

    onMounted(() => {
        if (props.items?.length && props.value && props.value !== props.items[0].code) {
            scrollToCenter(props.value);
        }
    });

    defineExpose({
        setValue
    });
</script>

<style lang="scss" scoped>
    .title {
        width: 100%;
        overflow-x: auto;
        -ms-overflow-style: none;
        scrollbar-width: none;
        display: flex;
        flex-direction: row;
        align-items: center;
        gap: 8px;
        white-space: nowrap;

        &::-webkit-scrollbar {
            display: none;
        }

        .skeleton-wrapper {
            overflow: hidden;
        }
            
        .title-text {
            font: var(--font-h6);
            font-weight: bold;
            color: var(--dark-500);
            flex-shrink: 0;
            margin-right: 12px;
            // white-space: normal;
            // max-width: 100%;
            // flex: 1;
        }

        .button {

        }

        .menu {
            flex-shrink: 0;
        }
    }
</style>
