<template>
    <div class="block">
        <TitleWithMenu
            :title="props.name"
            :items="data.types"
            :value="data.type"
            @change="setType"
        />

        <div class="slider-box">
            <Slider
                :colPreloadCount="4"
                :items="data.items"
                :itemsInCol="1"
                :loop="true"
                itemWidth="100%"
                itemHeight="60px"
                colAlign="center"
                colGap="8px"
                rowGap="8px"
                ref="sliderRef"
            >
                <template #default="{ item }">
                    <div class="slide" @click="open(item)">
                        <div v-if="!item.cover || !data.loadedItems.includes(item.code)" class="skeleton"></div>

                       <Img v-if="item.cover?.resized"
                            :preview="item.cover?.resized"
                            :original="item.cover?.original"
                            :alt="item?.title"
                            @load="handleLoad(item.code)"
                            @error="handleError(item.code)"
                            v-show="data.loadedItems.includes(item.code)"
                        />
                        <span class="name" v-if="data.loadedItems.includes(item.code)">{{ item.title }}</span>
                    </div>
                </template>
            </Slider>
        </div>
    </div>
</template>

<script setup>
    import { reactive, onMounted } from 'vue';
    import { useI18n } from 'vue-i18n';
    import { ref } from 'vue';
    import stores from '@/stores';
    import Img from 'ðŸ”—/components/ui/Img.vue';
    import { shuffleArray } from 'ðŸ”—/utils/utils';

    import TitleWithMenu from 'ðŸ”—/components/ui/TitleWithMenu.vue';
    import Slider from 'ðŸ”—/components/ui/Slider.vue';
    import api from 'ðŸ”—/api';


    const { t } = useI18n();

    const dict = api.dict();
    const nav = stores.nav();

    const sliderRef = ref();

    const props = defineProps({
        name: { type: [String, Object], default: 'pages.music.more-discoveries.title' }
    });

    const data = reactive({
        type: 'categories',
        items: [],
        loadedItems: [],
        types: [
            {
                page: 'category',
                code: 'categories',
                name: 'pages.music.more-discoveries.categories.title'
            },
            {
                page: 'genre',
                code: 'genres',
                name: 'pages.music.more-discoveries.genres.title'
            },
            {
                page: 'mood',
                code: 'moods',
                name: 'pages.music.more-discoveries.moods.title'
            }
        ]
    });

    const setType = async (type) => {
        data.type = type;

        const payload = await dict[type].all();
        data.items = shuffleArray(payload?.items || []);
        data.loadedItems = [];
    }

    const handleLoad = (code) => {
        data.loadedItems.push(code);
    }

    const handleError = (code) => {
        data.loadedItems.push(code);
    }

    const open = (payload) => {
        const page = data.types.find(item => item.code === data.type)?.page;

        if (!page) {
            return;
        }

        nav.open(page, { code: payload?.code });
    }

    onMounted(() => {
        setType(data.type);
    });
</script>

<style lang="scss" scoped>
    .block {
        padding: 0 0 var(--block-gap);
        display: flex;
        flex-direction: column;
        gap: 32px;
        width: 100%;
        justify-content: start;


        .title {
            padding: 0 16px;
        }

        .slide {
            position: relative;
            overflow: hidden;
            display: flex;
            align-items: center;
            justify-content: center;
            border-radius: 10px;
            width: 100%;
            height: 100%;

            img {
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                object-fit: cover;
                z-index: 0;
                filter: brightness(0.8) blur(3px);
            }

            .skeleton {
                @include skeleton;
            }

            .name {
                position: relative;
                z-index: 1;
                font: var(--font-h5);
                color: white;
                text-shadow: 0 0 4px rgba(0, 0, 0, 0.6);
            }
        }

        .slider-box {
            width: 100%;
            overflow: hidden;

            .slider {
                padding: 0px 16px;
                scroll-padding-left: 16px;
                scroll-padding-right: 16px;
            }
        }
    }
</style>
